@font-face {
    font-family: 'Nautilus Pompilius';
    src: local('Nautilus Pompilius'), local('NautilusPompilius'), url('../fonts/nautiluspompilius/nautiluspompilius.woff2') format('woff2'), url('../fonts/nautiluspompilius/nautiluspompilius.woff') format('woff'), url('../fonts/nautiluspompilius/nautiluspompilius.ttf') format('truetype');
    font-weight: 400;
    font-style: normal;
}

.header-wraper {
    position: fixed;
    top: 0;
}

.header-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    list-style: none;
    justify-content: center;
}

.header-item {
    margin-right: 50px;
    height: 80px;
    display: flex;
    align-items: center;
    justify-content: center
}

.header-item:first-child{
    margin-right: 10px;
}

.logo {
    width: 80px;
}

.header-link {
    font-family: 'Nautilus Pompilius', serif;
    color: #e95362;
    text-decoration: none;
    font-weight: 600;
    font-size: 28px;
    transition: all .3s ease-out;
}

.header-link:hover {
    color: #fff;
}