@font-face {
    font-family: 'Gagalin';
    src: local('Gagalin'), local('Gagalin-Regular'), url('/src/components/fonts/gagalin/Gagalin.woff2') format('woff2'), url('/src/components/fonts/gagalin/Gagalin.woff') format('woff'), url('/src/components/fonts/gagalin/Gagalin.ttf') format('truetype');
    font-weight: 800;
    font-style: normal;
}

.App {
  text-align: center;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-logo-title {
  font-weight: 700;
  color: #e95362;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 5s ease
    ;
  }
}

.App-header {
  font-family: 'Gagalin';
  background-color: #1A2232;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  0% {
    scale: 0.5;
  }
  50% {
    scale: 0.8;
  }
  100% {
    scale: 0.5;
  }
}
